@import "../../index";

.social-overlay {
    z-index: 10;
    position: fixed;

    bottom: 0;
    top: auto;

    left: calc(min(5vw, 50px));
    right: auto;

    width: 20px;

    .social-overlay-list {
        list-style: none;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-end;

        .social-overlay-item {
            padding: 10px;

            .social-overlay-item-icon-holder {
                color: $foreground-secondary;
                text-decoration: none;

                .social-overlay-item-icon {
                    font-size: $fz-icon;
                    transition: $transition;

                    &:hover {
                        color: $foreground-tertiary;
                        transform: translate(0, -3px);
                    }
                }
            }

            &:last-of-type {
                margin-bottom: 20px;
            }
        }

        &::after {
            content: "";
            width: 1px;
            height: 100px;
            background-color: $foreground-secondary;
        }
    }
}