@import "./index";

.main-content {
  width: 100%;
  padding: 0 15vw;

  counter-reset: heading-number 0;
}

.big-heading {
  font-size: clamp(40px, 8vw, 80px);
}

.numbered-heading {

  color: $foreground-primary;
  font-size: clamp(26px, 5vw, #{$fz-heading});

  &::before {
    position: relative;
    bottom: 4px;
    font-size: clamp(#{$fz-md}, 3vw, #{$fz-xl});
    font-weight: $fw-normal;
    content: "0" counter(heading-number) ".";
    color: $foreground-tertiary;
    margin-right: 10px;
    font-family: $font-family-mono;
    counter-increment: heading-number 1;
  }

  &::after {
    content: "";
    display: block;
    height: 1px;
    width: 100%;
    background-color: $foreground-secondary;
  }

}